.WhatWeDo00 {
  text-align: center;
  /* margin-bottom: 10%; */

}

.background-div {
  background-color: #f7f7f7;
  padding: 10px;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  position: relative;
}

.heading {
  margin-bottom: 20px;
}

.heading h2 {
  font-family: FONTSPRING DEMO - Proxima Nova Cond;
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0em;
  text-align: center;
  color: #004059;
}

.heading h3 {
  font-family: Jost;
  font-size: 24px;
  font-weight: 500;
  line-height: 35px;
  letter-spacing: 0em;
  text-align: center;
  color: #455a64;
}

.heading p {
  font-family: Jost;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0px;
  text-align: center;
  color: #263238;
  width: 70%;
  margin: auto;
}

.card-WhatWeDo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: calc(33.33% * 3 + 20px * 6);
  margin: 0 auto;
  overflow: hidden;
  margin: 10px;
}

.cardWhatWeDo {
  background-color: #fff;
  animation: fadeIn 0.5s ease forwards;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 10px;
  padding: 20px;
  transition: background-color 0.3s ease;
  flex: 1 0 calc(33.33% - 20px * 3);
  max-width: calc(33.33% - 20px * 3);
  text-align: left;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.cardWhatWeDo:hover {
  background-color: #10baac;
  color: #fff;
}

.cardWhatWeDord-logo {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

.cardWhatWeDord-heading {
  margin-bottom: 10px;
  color: #455a64;
  font-family: Inter;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0em;
}

.cardWhatWeDo-description {
  margin-bottom: 15px;
  font-family: Jost;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: left;
  color: #64748b;
}

.read-more {
  background-color: #10baac;
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.read-more:hover {
  background-color: #0b7d73;
}

